import { useEffect, useState } from "react";
import { AssuntoType } from "@/types/assuntotype";
import { Assuntos } from "@/data/assuntos";

    export const [ today, setToday ] = useState<AssuntoType[]>([]);
    export const [ tomorrow, setTomorrow ] = useState<AssuntoType[]>([]);
    export const [ afterTomorrow, setAfterTomorrow ] = useState<AssuntoType[]>([]);
    export const [ nextDays, setNextDays ] = useState<AssuntoType[]>([]);

    const hoje = new Date();
    const amanha = hoje.getDate() + 1;
    const depoisAmanha = hoje.getDate() + 2;
    const proximosDias = hoje.getDate() + 3;

    useEffect(() => { 
        Assuntos.forEach((assunto) => {
    if(assunto.dataAgendada.getDate() === hoje.getDate()){
        setToday(today => [...today, assunto]);
    } else if(assunto.dataAgendada.getDate() === amanha){
        setTomorrow(tomorrow => [...tomorrow, assunto]);
    } else  if(assunto.dataAgendada.getDate() === depoisAmanha){
        setAfterTomorrow(afterTomorrow => [...afterTomorrow, assunto])
    } else if(assunto.dataAgendada.getDate() >= proximosDias){
        setNextDays(nextDays => [...nextDays, assunto])
    }
        });
    }, [])